@use 'base/variables' as v;
@use 'base/mixins' as mix;
@use 'sass:color';

.header{
    min-height: 70rem;
    background-image: 
    url(../img/grafico.svg), 
    url(../img/grafico.svg), 
    linear-gradient(to right, rgba(v.$negro, 1), rgba(v.$negro, .5)),
    image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
    background-repeat: no-repeat;
    background-size: 400px, 400px, cover, cover;
    background-position: top left, bottom right, center center, center center;

    &__contenedor{
        @include mix.contenedor;
    }
    &__navegacion{
        padding: 5rem 0;
        display: flex;
        justify-content: center;
        gap: 2rem;
        @include mix.tablet{
            justify-content: flex-end;
        }
    }
    &__enlace{
        @include mix.enlace;
    }
    &__contenido{
        padding-top: 10rem;

        @include mix.tablet{
            padding-top: 5rem;
        }
    }  
    &__logo{
        @include mix.texto-fluido(1rem, 8rem);
        @include mix.texto-degradado(v.$azul, v.$secundario);
        text-align: center;
        @include mix.tablet{
            text-align: left;
            display:  inline-block;
        }
    }
    &__texto{
        font-size: 3rem;
        font-weight: v.$bold;
        color: v.$blanco;
        margin: 2rem 0;
        text-align: center;
        text-transform: uppercase;

        @include mix.tablet{
            text-align: left;
        }
        &--modalidad{
            font-size: 2rem;
        }
    }
    &__boton{
        @include mix.boton(v.$secundario, v.$blanco);
        text-transform: uppercase;
        margin: 2rem 0;

        @include mix.tablet{
            width: auto;
        }
    }



}