@use 'variables' as v;
@use 'mixins' as m;

h1, h2, h3 {
    @include m.heading;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
.text-center {
    text-align: center;
}
// selector que busca todos los que finalizan con heading.
[class$="__heading"]{
    // 
    @include m.nombre-pagina;
}
