@use 'base/variables' as v;
@use 'base/mixins' as mix;
@use 'sass:color';

.formulario{
    display: flex;
    flex-direction: column;
    &__contenedor-icono{
        display: flex;
        border-radius: .5rem;
        overflow: hidden;
    }
    &__icono{
        background-color: v.$gris-oscuro;
        width: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        i{
            color: v.$blanco;
            font-size: 2rem;

        }

    }
    &__campo{
        display: flex;
        flex-direction: column;
        gap: 1rem; 
        margin: 1.5rem 0;  
    }
    &__label{
        display: block;
        font-size: 1.8rem;
        color: rgba(v.$negro, .8);
    }
    &__radio{
        display: flex;
        gap: 2rem;
        
    }
    &__input,
    &__select{
        padding: 1rem;
        border: 1px solid color.adjust(v.$gris-claro, $lightness: -20%);
        border-radius: .5rem;
        &--file{
            border: none;
        }
        &--sociales{
           @extend .formulario__input;
           flex: 1;
           border-top-left-radius:0; 
           border-bottom-left-radius:0; 

        }
    }  
    // formulario de crear
    &__submit{
        @include mix.boton(v.$azul, v.$blanco);
        width: auto;
        align-self: flex-start;
        &--registrar{
            width: 100%;
        }
    }

    &__fieldset{
        border: none;
        margin-top: 1rem;
        padding: 0;
    }

    &__legend{
        columns: v.$gris;
        font-size: 2.2rem;
        margin-bottom: 2rem;

    }
    // tags 
    &__listado{
        @include mix.tags;
        cursor: pointer;
    }

    &__texto{
        margin-top: 3rem;
    }
    &__imagen{
        max-width: 20rem;
    }
}